#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

.swiperobject-tma {
  background: #101010;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
}

.swiperobject-tma .swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiperobject-tma .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 800px;
}

.swiperobject-tma .swiper-slide img {
  display: block;
  width: 100%;
  border-radius: 20px;
}
